	import Vue from 'vue'
	import moment from 'moment'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	import XPage from 'c%/page'
	
	export default{
		mixins: [ common,globals ],
		components: {
			XTable,
			XPage
		},
		props: {
			bids: {
				type: Array,
				default: []
			},
			group: {
				type: String,
				default: 'report_performances'
			},
			yearMonth: {
				type: String,
				default: moment(new Date()).format('YYYY-MM')
			}
		},
		data () {
			return {
				data: [],
				total: 0,
				pageIndex:1,
				pageSize:10
			}
		},
		watch: {
			bids: function (val) {				
				this.init_data()
			},
			group: function (val) {				
				this.pagenation(1)
			},
			yearMonth: function (val) {
				this.pagenation(1)
			}
		},
		mounted () {
			this.init_data()
		},
		methods: {
			pagenation (page) {
				this.pageIndex = page            
            	this.init_data()     
			},
			init_data () {
				let params = {
					page: this.pageIndex,
					pagesize: this.pageSize
				}
				this.showLoading()
				this.$rest(this.data_uri).get(params)
				.success(data=>{
					this.data = data.list
					this.total = data.total
                	this.pageSize = parseInt(data.pagesize)
					this.loadFinished()
				})
			},
			loadFinished () {
				let data = this.data
				
				if(data&&data.length>0){
					data.push({
						column_total:true
					})
				}
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
			//获取列统计
			getTotal(h,field) {
				let data = this.data,
					result = 0
				if(data&&data.length>0){
					data.forEach(d=>{
						if(d[field]){							
							result += Number(d[field])							
						}
					})
					
					if(!_.isNumber(data[data.length-2][field])){
						result = parseFloat(result).toFixed(2)	
					}
				}
				return h('span',{'class':'text-danger'},result)
			}
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + 55 + this.headerHeight) 
			},
			columns () {
				let columns = []			
				let column_eid = {
						title: '工号',
						key: 'eid',
						width: 100						
					},					
					column_ename = {
						title: '姓名',
						key: 'ename',
						width: 100,
						fixed: 'left',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							return this.$filter('ename')(params.row.eid)
						}
					},
					column_sale_role_did = {
						title: '签单角色',
						key: 'sale_role_did',
						width: 120,
						render: (h,params)=>{							
							return this.labelDicts(params.row.sale_role_did,'sale_role') 
						}
					},
					column_total_amount = {
						title: '签单金额',
						key: 'total_amount',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
                        		return this.getTotal(h,'total_amount')
                        	}
							return params.row.total_amount
						}
					}					
				
				if(this.group=='report_performances/employee_receipts'){
					column_sale_role_did.title = '回款角色'
					column_total_amount.title = '回款金额'
				}
				
				columns.push(column_ename,column_eid,column_sale_role_did,column_total_amount)		
				return columns
			},
			data_uri () {
				let start_date = moment(this.yearMonth+'-01').format('YYYYMMDD'),
					end_date = moment(this.yearMonth+'-'+moment(this.yearMonth).daysInMonth()).format('YYYYMMDD'),
					base_uri = this.group,					
					uri = '',
					bids = ''

					if(this.bids.length){
						bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
					}
					
				uri = util.sprintf('%s?group=eid,sale_role_did&create_time=[between,%s,%s]%s',base_uri,start_date,end_date,bids)
					
				return uri
			}
		}
	}



